* {
  margin: 0;
  padding: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  font-family: "Pragmatica", "Arial", sans-serif;
  height: 100vh; }
  @media (max-width: 1199px) {
    body {
      display: block;
      height: auto;
      width: 100vw;
      overflow-x: hidden; } }

.main {
  height: 90vh; }

.main__wrapper {
  display: inline-flex;
  overflow-y: hidden;
  align-items: flex-start;
  height: 100vh;
  width: 100%;
  position: relative; }
  @media (max-width: 1199px) {
    .main__wrapper {
      display: block;
      height: auto;
      width: 100vw;
      background-image: url("../img/bg.png");
      background-size: 100vw 100vh;
      background-attachment: fixed;
      background-repeat: no-repeat; } }

.main__section {
  width: 90vw;
  padding-left: 10vw;
  padding-bottom: 100%;
  background-size: cover;
  padding-bottom: 3vh;
  padding-top: 18vh; }
  @media (max-width: 1199px) {
    .main__section {
      padding-left: 5vw; } }

.wrapper {
  width: 100vw;
  background-image: url("../img/bg.png");
  height: 100%;
  background-size: cover;
  background-attachment: scroll; }

.sprite {
  display: none; }

.container {
  max-width: 1300px;
  width: 90%;
  margin: 0 auto; }
  @media (max-width: 1199px) {
    .container {
      width: 98%; } }

*, *:before, *:after {
  box-sizing: border-box; }

html, body {
  height: 100%;
  width: 100%; }

*, *:before, *:after {
  box-sizing: border-box; }

.header {
  z-index: 80;
  padding-left: 10vw;
  width: 95vw;
  height: 10vh;
  position: fixed;
  z-index: 100; }
  @media (max-width: 1199px) {
    .header {
      height: 15vh;
      padding-top: 10vh; } }

.header__wrapper {
  width: 80vw;
  height: 80vh;
  padding-bottom: 5vh;
  height: 10vh;
  width: 95vw;
  padding-bottom: 3vh;
  padding-top: 3vh;
  vertical-align: middle;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start; }
  @media (max-width: 1199px) {
    .header__wrapper {
      height: auto;
      width: 80vw;
